.casestudy_filter_container {
  display: flex;
  align-items: center;
  @media #{$md, $sm, $xs, $xss} {
    row-gap: 10px;
  }
}
.casestudy_filter {
  display: flex;
  align-items: center;
  height: 100%;
  @media #{$md, $sm, $xs, $xss} {
    margin-bottom: 40px;
    row-gap: 10px;
  }
  .filter {
    display: flex;
    width: 100%;
    align-items: center;
    justify-content: end;
    gap: 20px;
    @media #{$md, $sm, $xs, $xss} {
      justify-content: space-around;
    }
    p {
      color: black;
      margin: 0;
      width: fit-content;
      width: 100px;
    }
    select {
      height: 50px;
      width: 250px;
      outline: none;
      border: 1px solid #eee;
      border-radius: 5px;
      padding: 0 10px 0 10px;
      @media #{$md, $sm, $xs, $xss} {
        width: 100%;
      }
    }
  }
}
